        height: 80px;
    }
    .captcha input{
        border-radius: 20px;
        line-height: 30px;
        width: 240px;
        font-size: 14px;
    }

    .loginButton{
        margin-top: 12px;
    }
    .loginButton button{
        margin: 0 auto 20px;
        width: 240px;
        height: 50px;
        border-radius: 20px;
        font-size: 20px;
    }

    .contactLinks{
        margin: 20px 0 0px;
        font-size: 25px;
    }
