@media all and (max-width: 600px) {
    .container{
        max-width: 600px;
        max-height: 700px;
        margin: 100px auto;
    }

    .aboutUs{
        display: none;
    }

    .login{
        width: 300px;
        height: 400px;
    }

    .contactLinks{
