    }
}

@media all and (max-width: 400px) {
    .contactLinks{
        margin: 10px 0 0px;
        font-size: 18px;
    }
    .contactLinks span{
        top:14px;
    }
    
    .userInfo{
        display: flex;
        font-size: 12px;
    }
}