*{
  transition: 0.2s;
}

.sidebar {
    background: white;
    width: 20px;
    padding: 20px;
    font-size: 20px;
    font-family: "Popins", sans-serif;
    overflow: hidden;
    z-index: 1;
    position: fixed;
    height: 100vh;
    top:0px;
    padding-top: 80px;
    box-shadow: 0 0 8px rgb(135 132 132 / 20%);
}
.sidebar:hover {
    width: 210px;
    transition: 0.5s;
}
.sidebar a{
    overflow: hidden;
    color: black;
    text-decoration: none;
    display: block;
    line-height: 20px;
    width: 100%;
    margin-left: 0px;
    white-space: nowrap;
}
.sidebar a:hover{
  color: red;
}
.sidebarImg img {
    display: inline-block;
    width: 100px;
    height: 100px;
    border-radius: 50px;
    margin-bottom: 40px;
    margin-left: 20px;
    margin-top: 50px;
}
.sidebarImg h3{
    color: #999999;
    font-size: 20px;
    text-transform: uppercase;
    letter-spacing: 2px;
    margin-top: 0px;
    margin-bottom: 5px;
    margin-left: 40px;
}

@media all and (max-width: 850px) {
  .sidebar {
    display: none;
    margin: 0;
    padding: 0;
  }
}
